import * as React from 'react';
import { Layout, Collapse, Divider } from 'antd';
// import NetUtils from '../../utils/NetUtils';
// import ApiUtils from '../../utils/ApiUtils';

interface IQaState
{
    apiList? : string[];
}

class Qa extends React.Component<{} , IQaState>
{
    public constructor(props : any)
    {
        super(props);
        this.state = {
        };
    }

    public render()
    {
        return (
            <Layout style={{alignItems : "center", justifyContent : "flex-start"}}>
            
                <Divider>Q&amp;A</Divider>
                <Collapse style={{width: "70vw", margin: 15}} accordion={true}>
                
                    <Collapse.Panel header="" key="1">
                        <p></p>
                    </Collapse.Panel>
                    <Collapse.Panel header="" key="2">
                        <p><strong></strong></p>
                    </Collapse.Panel>
                    <Collapse.Panel header="" key="3">
                        <p>Google
                            </p>
                    </Collapse.Panel>
                </Collapse>

                <Divider dashed={true}>API</Divider>
                <Collapse style={{width: "70vw", margin: 15}} accordion={true}>
                    
                    <Collapse.Panel header="" key="1">
                        <p style={{marginLeft: 20}}>:</p>
                        <p style={{marginLeft: 20}}>Google: google-satellite </p>
                        <p style={{marginLeft: 20}}>Google: google-image</p>
                        <p style={{marginLeft: 20}}>Google: google-image</p>
                        <p style={{marginLeft: 20}}>: amap-satellite </p>
                        <p style={{marginLeft: 20}}>: amap-image </p>
                        <p style={{marginLeft: 20}}>: amap-cover </p>
                        <p style={{marginLeft: 20}}>: tianditu-satellite </p>
                        <p style={{marginLeft: 20}}>: tianditu-image </p>
                        <p style={{marginLeft: 20}}>: tianditu-cover </p>

                    </Collapse.Panel>

                    <Collapse.Panel header="" key="2">
                        <p>1-18</p>
                    </Collapse.Panel>

                    <Collapse.Panel header="API" key="3">
                        <p><strong>/init/initMap <a color="#FA0">POST</a>, </strong></p>
                        <p style={{marginLeft: 20}}>left : double, , </p>
                        <p style={{marginLeft: 20}}>right : double, , </p>
                        <p style={{marginLeft: 20}}>top : double, , </p>
                        <p style={{marginLeft: 20}}>bottom : double, , </p>
                        <p style={{marginLeft: 20}}>type : string, , <strong></strong></p>
                        <p style={{marginLeft: 20}}>level : int, , <strong></strong></p>
                    </Collapse.Panel>

                    <Collapse.Panel header="" key="4">
                        <p><strong>server/config <a color="#FA0">GET</a>, </strong></p>
                    </Collapse.Panel>

                    <Collapse.Panel header="" key="5">
                        <p><strong>{"server/map/{type}/{x}/{y}/{z}"} <a color="#FA0">GET</a>, URL:</strong></p>
                        <p style={{marginLeft: 20}}>type : string, , <strong></strong></p>
                        <p style={{marginLeft: 20}}>{"x : int, /, TMS()x"}</p>
                        <p style={{marginLeft: 20}}>{"y : int, /, TMS()y"}</p>
                        <p style={{marginLeft: 20}}>{"z : int, /, TMS()z"}</p>
                    </Collapse.Panel>
                </Collapse>
            </Layout>
        );
    }
}

export default Qa;


